@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
.links {
    margin: 10px;
    cursor: pointer;
}
.active {
    border-bottom: 2px solid palegreen;
}
.form {
    font-family: 'Nunito', sans-serif;    
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 200px;
    margin-top: 30px;
    background: none;
    color: white;
    border: 0;
    text-align: center;
    margin: 20px auto;
    border: 2px solid #260f66;
    border-radius: 24px;
    transition: 0.25s;
    padding: 20px;
}
.form:focus{
    width: 250px;
    border-color: #a035df;
}
.desc {
    font-family: 'Nunito', sans-serif;    
    background: none;
    border: 0;
    color: white;
    border: 2px solid #260f66;
    border-radius: 5px;
    transition: 0.25s;

}
.btn {
    font-family: 'Nunito', sans-serif;    
    height: 30px;
    width: 130px;
    margin-top: 30px;
    background: none;
    color: white;
    border: 0;
    text-align: center;
    margin: 20px auto;
    border: 2px solid #34db95;
    border-radius: 24px;
    outline: none;
    transition: ease-out 0.3s;
    text-align: center;
    position: relative;
    z-index: 1;  
}
.btn:hover {
    color: #fff;
    cursor: pointer;
    background-color: #34db95;
}

::-webkit-input-placeholder {
    color: #818182;
}

:-ms-input-placeholder {
    color: #818182;
}

::placeholder {
    color: #818182;
}
.container {
    background-color: none;
    background: none;
}
.ContactMe {
    font-family: 'Nunito', sans-serif;    
    color: white;
}
